import React from 'react';
import Wheel from './Wheel';
import Screen from './Screen';
import ZingTouch from 'zingtouch';


class App extends React.Component {
  
 constructor(){
  
   super();
   this.state={
    
     hide:false,
     hour:'',
     minutes:'',
     mainmenu:[
       'active',
       '',
      '',
       ''
     ],
     submenu:[
      '',
      '',
     '',
      ''
    ],
    

   }
 }

componentDidMount(){
  
  var d=new Date();
  this.setState({
   hour:d.getHours(),
   minutes:d.getMinutes()
  })  
}
/*Function called to change active class of all menu options using zingtouch */
handleRotate=()=>{
  
  let angle = 0;
  const {mainmenu}=this.state;
  const target =document.getElementById('outer-circle');
  const zing = new ZingTouch.Region(target);

  let previous=0;
  let index=0;

for(let i=0;i<mainmenu.length;i++){
  
if(mainmenu[i]==='active'){
  index=i;
  
  
}
}


  zing.bind(target,'rotate',(e)=>{
    console.log(angle,'harsh');
   // console.log('tapi');
    angle = angle + e.detail.distanceFromLast;
    
    if(angle>45+previous||angle<-45+previous){
      
      mainmenu[index]='';
     if(angle>45+previous) index=index+1;
     else index=index-1;
      if(index===mainmenu.length){
        index=0;
      }
      if(index===-1){
        index=mainmenu.length-1;
      }
      for(let i=0;i<mainmenu.length;i++){
        if(i!==index){
      mainmenu[i]='';
        }
      else {
        mainmenu[index]='active';
      }
      }
   
      
        this.setState({
           mainmenu:mainmenu
        })
        previous=angle;
        
      
        //console.log(mainmenu);
    }
   
  })

  
 
  
 // console.log(mainmenu);

}


/*Function to display the menu screen whenever we hover upon the wheel */
   cs=()=>{
    
    const {hide}=this.state;
     return hide;
   }
   hideshow=()=>{
    const {hide}=this.state;
    var pr=hide;
    pr=!pr;
    this.setState({
      hide:pr
    })
   // console.log(hide);
  }
  /*function used when we click the inner wheel for performing the task */
handlecentre=()=>{

  const {mainmenu,submenu}=this.state;

if(submenu[1]==='visible'){
  submenu[0]='visible';
  submenu[1]='';
  submenu[2]='';
  submenu[3]='';
  this.setState({
    submenu:submenu
  })
  return;
}

for(let i=0;i<mainmenu.length;i++){
  if(mainmenu[i]==='active'){
    submenu[i]='visible'
  }
  else{
    submenu[i]='';
  }
}

this.setState({
  submenu:submenu
})
console.log(submenu);
}
/*whenever the menu button(div) is clicked */
handlemenuclick=()=>{
  
  const {mainmenu,submenu}=this.state;
for(let i=0;i<submenu.length;i++){

    submenu[i]='';
  
}

this.setState({
  submenu:submenu
})
}
/*for pause play of song  */
handleplaypause=()=>{
  var ele=document.getElementById('music-cover');
  if(!ele.paused){
    ele.pause();
  }
  else  ele.play();
 
}


  render(){

    {/*update time */}
    {setInterval(() => {
      var d=new Date();
   this.setState({
    hour:d.getHours(),
    minutes:d.getMinutes()
   })  
    //  console.log(1);
    },60000);}
    const {mainmenu,submenu,playpause}=this.state;
  return (



    <div className="App">
      <Screen hm={this.cs()}
     Hour={this.state.hour}
     Minutes={this.state.minutes}
     menuactive={mainmenu}
     submenu={submenu}
     />
      <Wheel hs={this.hideshow}
       onhandleRotate = {this.handleRotate}
       onCentreclick ={this.handlecentre}
       onMenuclick={this.handlemenuclick}
       onplaypause={this.handleplaypause}
     />
 


    </div>
  );

  }
}

export default App;
