import React from 'react';

/*All code for the wheel component goes here */

const Wheel= (props)=> {
  console.log(111);

    return(

<div className= "outer">
 {/*outer wheel and its buttons including the innere wheel */}
<div className="outer-wheel" onMouseEnter={props.hs} onMouseLeave={props.hs} onMouseOver={props.onhandleRotate}
 id="outer-circle">

    <div className="menu effect" onClick={props.onMenuclick}>Menu</div>
    <div className="forward effect"><img src="https://www.flaticon.com/svg/static/icons/svg/709/709533.svg"></img></div>
    <div className="backward effect"><img src="https://www.flaticon.com/svg/static/icons/svg/2413/2413268.svg"></img></div>
    <div className="play-pause effect" onClick={props.onplaypause}><img src="https://www.flaticon.com/premium-icon/icons/svg/3031/3031722.svg"></img></div>
    <div className="inner-wheel" onClick={props.onCentreclick}>

    </div>
</div>

</div>

    );
}

export default Wheel;